body {
  width: 100%;
  height: 100vh;
  font-family: var(--inconsolata);
  font-size: 1.25rem;
  font-weight: var(--weight-400);
  background-color: var(--button-text);
  color: var(--text-white);
}

:root {
  /*Neutral*/
  --text-white:hsl(0, 0%, 100%);
  --hint-text: hsl(252, 6%, 83%);
  --form-border:hsl(245, 15%, 58%);
  --form-background: hsla(245, 15%, 58%, 0.2);
  --upload-icon:hsla(245, 15%, 58%, 0.3);
  --border:hsl(245, 19%, 35%);
  --button-text:hsl(248, 70%, 10%);
  /*Button*/
  --button:hsl(7, 88%, 67%);
  --button-hover:hsl(7, 71%, 60%);
  /*Gradient*/
  --gradient-start:hsl(7, 86%, 67%);
  --gradient-end:hsl(0, 0%, 100%);
}

:root {
  /*Font Family*/
  --inconsolata: "Inconsolata", serif;
  /*Font Weights*/
  --weight-400:400;
  --weight-500:500;
  --weight-700:700;
  --weight-800:800;
}

/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}
body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input, button, textarea, select {
  font: inherit;
}

/* 8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}

header {
  display: flex;
  justify-content: center;
}

.wrapper__form {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.wrapper__title {
  line-height: 1.1;
}
.wrapper__spot {
  color: var(--hint-text);
}

