.wrapper__avatar {
  text-align: start;
}

.upload {
  background-color: var(--form-background);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  margin: 0.5rem 0;
  border: 0.125rem dashed var(--form-border);
  border-radius: 1rem;
  gap: 1rem;
}
.upload__icon {
  background-color: var(--upload-icon);
  border: 0.0625rem solid var(--form-border);
  border-radius: 0.5rem;
  padding: 0.5rem;
  width: 100%;
  height: 100%;
  max-width: 3.125rem;
  max-height: 3.125rem;
}
.upload__text {
  font-size: 1.125rem;
  color: var(--hint-text);
}
.upload__input {
  display: none;
}
.upload__actions {
  display: none;
}
.upload__note {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  font-size: 0.8125rem;
  color: var(--hint-text);
}

.button {
  background-color: var(--upload-icon);
  color: var(--hint-text);
  border: none;
  border-radius: 0.3rem;
  text-decoration: underline;
}